<template lang="pug">
section(class="services")
    div(class="container")
        div(class="title")
            h2 {{ $t("our-services") }}
            p {{ pageServices.servicesDesc }}
        div(class="services-content")
            div(class="row")
                div(class="col-md-6 col-lg-3 box" v-for="service in pageServices.services", :key="service.id")
                    router-link(:to="`/services/${service.url}`")
                        div(class="services-box wow fadeInUp" data-wow-duration="1s")
                            div(class="services-box-icon")
                                img(:src="service.image" class="img-fluid" alt="")
                            div(class="services-box-details")
                                h3 {{ service.title }}
                                div(class="service-text" v-html="service.body")
</template>

<script>
export default {
    name: "Services",
    props: ["pageServices"],
};
</script>
