<template lang="pug">
    div(class="page")
        Header
        Services(:pageServices="this.ServicesData")
        ProjectSteps
        Footer
</template>

<script>
import { mapState } from "vuex";
// @ is an alias to /src
import Header from "@/components/global/Header.vue";
import Services from "@/components/pages/Services.vue";
import ProjectSteps from "@/components/home/ProjectSteps.vue";
import Footer from "@/components/global/Footer.vue";

export default {
    name: "ServicesPage",
    components: {
        Header,
        Services,
        ProjectSteps,
        Footer,
    },
    metaInfo() {
        return {
            title: process.env.VUE_APP_TITLE,
            titleTemplate: `%s | ${this.ServicesData.website_title}`,
            meta: [
                { charset: "utf-8" },
                {
                    name: "description",
                    content: this.ServicesData.meta_description,
                },
                {
                    name: "keywords",
                    content: this.ServicesData.meta_keywords,
                },
            ],
        };
    },
    async created() {
        await this.$store.dispatch("getServicesData");
    },
    computed: {
        ...mapState(["ServicesData"]),
    },
};
</script>
